import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'

import { mapGetters } from 'vuex'
import axios from '@/api/axiosApi'

export default {
  data() {
    return {
      addressInfo: {},
      amusementList: []
    }
  },
  components: {
    VHeader, Scroll
  },
  computed: {
    ...mapGetters([
      'currentArea'
    ])
  },
  created () {
    this._getAddressInfo()
    this._getAmusementList()
  },
  activated () {
    this._getAmusementList()
  },
  methods: {
    _getAddressInfo() {
      axios.get(this, `/v1/addressInfo/${this.$route.params.addressId}`, null, (data) => {
        this.addressInfo = data
      })
    },
    _getAmusementList() {
      axios.get(this, '/v1/localAmusement', {
        addressId: this.$route.params.addressId,
        categoryId: this.$route.params.categoryId
      }, (data) => {
        this.amusementList = data
      })
    },
    clickItem (item) {
      axios.put(this, `/v1/localAmusement/readCount/${item.id}`)
      this.$router.push(`/content/${item.contentId}/${item.name}`)
    }
  }
}



